import { Alert, Accordion, Form, Button, Container, Row, Navbar, Col, Badge, Stack } from 'react-bootstrap';
import SSC from 'sscjs';
import { useState } from 'react';
import NFTCard from './NFTCard';
import NFTCardList from './NFTCardList';

function CardTrade() {
    const [ cargando, setCargando ] = useState(false);
    const [loggedIn, setLoggedIn] = useState(false);
    const [ data, setData ] = useState([]);
    const [ username, setUsername ] = useState("elloco10");

    const changeUsername = (e) => {
        setUsername(e.target.value);
    }


    const fetchData = async (offset) => {
        let ssc_script = new SSC('https://custr.de.ryamer.com');
        if (ssc_script !== null) {
            let data = await ssc_script.find(
                "nft", //contract
                "STARinstances", //table
                { account: username }, //query
                1000, //limit
                offset, //offset
                [] //indexes
            );

            return data;
        }else{
            console.log('sin ssc');
            return [];
        }
    }

    const loadAllCards = (offset, cards) => {
        console.log('*** loadAllCards offset = ', offset);
        fetchData(offset).then((data)=>{
            // totalCartas += data.length;
            console.log('*** '+offset+' - data = ', data);
            if (data !== null && data.length>0) {
                // const data = parseData(data, lucks);
                cards = [...cards, ...data];
                loadAllCards(offset + data.length, cards);
            } else {
                console.log('*** final cards = ', cards);
                setData(cards);
                setCargando(false);
            }
        });
    }

    const login = () => {
        // setUsername(e.target.value);
    }

    const logAndCollect = () => {
        login();

        if (loggedIn) {
            setCargando(true);
            try {
                loadAllCards(0, []);
            } catch (e) {
                // setError(e);
                console.error(e);
            }
        }
    }

    return (<>
        <Row>
            <Col>
                <Form onSubmit={e => { e.preventDefault(); }}>
                <Form.Group className="mb-3" controlId="fieldGroup">
                    <Form.Label>RisingStar Username</Form.Label>
                    <Form.Control type="username" placeholder="Enter your username" value={username} onChange={changeUsername}/>
                </Form.Group>
                <Form.Group className="mb-3" controlId="buttonGroup">
                    <Button variant="primary" type="submit" onClick={logAndCollect}>Calculate</Button>
                </Form.Group>
                </Form>
            </Col>
        </Row>
        <Row>
            <Col>
                <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                    {/* {cargando ? <div>Loading your cards...</div> : <NFTCardList cards={data}/>} */}
                    {cargando ? <div>Loading your cards...</div> : data.map((card, i) => {
                        // console.log(card.properties.type);
                        return (<NFTCard key={i} cardName={card.properties.type}/>);
                    })}
                    {/*
                        - Seleccionar cartas a cambiar (origen)
                        - Seleccionar carta por la cual cambiar (destino)
                     */}
                </div>
            </Col>
        </Row>
    </>);
}

export default CardTrade;